<template>
    <div>
        <el-card class="box-card" v-loading="loading">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/form' }">商品列表</el-breadcrumb-item>
            </el-breadcrumb>
            <el-divider></el-divider>
            <el-row>
                <el-col :span="6">
                    <el-form :inline="true" :model="form">
                        <el-form-item >
                            <el-input
                            placeholder="查询商品名称"
                            clearable
                            v-model="form.search"
                            ></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button 
                            type="primary"
                            @click="searchName">查询</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-col :span="1" :offset="16">
                    <el-button 
                    type="primary"
                    @click="fetch()"
                    >
                    <i class="el-icon-refresh"></i>
                    刷新</el-button>
                </el-col>
            </el-row>
            <el-table
                :data="tableData.slice((currentPage - 1) * pagesize, currentPage*pagesize)"
                border
                style="width: 100%"
                >

                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-row>
                                <el-col :md="12">
                                    <el-form-item label="创建时间">
                                        <template>
                                            <i  class="el-icon-time" /> <span>{{props.row.createdAt | date}}</span>
                                        </template>
                                    </el-form-item>
                                </el-col>
                                <el-col :md="12">
                                    <el-form-item label="修改时间">
                                        <template>
                                            <i  class="el-icon-time" /> <span>{{props.row.updatedAt | date}}</span>
                                        </template>
                                    </el-form-item>
                                </el-col>
                                <el-col :md="12">
                                    <el-form-item label="浏览次数">
                                        <i class="el-icon-view"></i> <span class="color-green">{{ props.row.browse }}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :md="24">
                                    <el-form-item label="详情轮播图">
                                        <template>
                                            <el-row>
                                                <el-col :md="6" style="margin:0 10px;"
                                                 v-for="item in props.row.skills" :key="item._id">
                                                 <el-image :src="item.image">
                                                </el-image>
                                                </el-col>
                                            </el-row>
                                        </template>
                                    </el-form-item>
                                </el-col>
                            </el-row>

                        </el-form>
                    </template>
                    </el-table-column>


              
                <el-table-column type="index" width="55">
                </el-table-column>
                <el-table-column  prop="_id" label="编号" width="230">
                </el-table-column>
                
                <el-table-column label="商品分类" width="100">
                    <template slot-scope="scope">
                        <el-tag>{{scope.row.categoryes}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="name" label="商品名称" width="180" >
                </el-table-column>
                <el-table-column label="图片" width="120">
                    <template slot-scope="scope">
                        <img :src="scope.row.image" alt="" style="width:98px;">
                    </template>
                </el-table-column>
                <el-table-column label="价格" width="110">
                    <template slot-scope="scope">
                        ￥：{{scope.row.price}}
                    </template>
                </el-table-column>
                
                 <el-table-column  prop="stock" label="库存" width="80">
                </el-table-column>
                 <el-table-column prop="user" label="推荐指数" width="180">
                     <template slot-scope="scope">
                         <el-rate
                            v-model="scope.row.recommend"
                            disabled
                            show-score
                            text-color="#ff9900"
                            score-template="{value}">
                        </el-rate>
                     </template>
                </el-table-column>
                <el-table-column label="状态" width="100" >
                    <template slot-scope="scope" class="state-item">
                        <el-tag type="success" v-if="scope.row.state == true" >上架中</el-tag>
                        <el-tag type="warning" v-else-if="scope.row.state == false">待上架</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="创建时间" >
                    <template slot-scope="scope">
                        <i  class="el-icon-time" /> <span>{{scope.row.createdAt | date}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label="operation"
                width="220"
                 v-if="flag > 10 ? true : false">
                <template slot-scope="scope">
                    <el-button type="primary" 
                    size="small"
                    @click="goOnline(scope.row)">
                    上架
                    </el-button>
                    <el-button type="warning" 
                    size="small"
                    @click="$router.push(`/create-commodity/edit/${scope.row._id}`)">
                    编辑
                    </el-button>
                    <el-button type="danger" 
                    @click="remove(scope.row)" 
                    size="small">
                    删除
                    </el-button>
                </template>
                </el-table-column>
            </el-table>

            <div class="block">
                <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[1,5,10,20,50]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableData.length">
                </el-pagination>
            </div>
        </el-card>
    </div>
</template>
<script>
import dayjs from 'dayjs'      
export default {
    filters:{
         // 转换日期格式
        date(val){
        return dayjs(val).format("YYYY-MM-DD hh:mm")
        }
    },
    data() {
        return {
        flag:'',
        loading:true,
        currentPage:1,
        pagesize:10,  
        tableData: [],
        form:{
            search:''
        }
        }
    },
    created(){
        setTimeout(()=>{
            this.loading = false
        },500)
        this.flags()
        this.fetch()
    },
    methods: {
        async fetch(){
            const res = await this.$http.get('/goods')
            this.tableData = res.data
        },
        //   删除
        async remove(row){
            const res = await this.$http.delete(`/goods/${row._id}`)
            this.$message({
                type:'success',
                message:'删除成功'
            })
            this.fetch()
        },
        //  上架
        async goOnline(e){
            e.state = true
            const item = await this.$http.put(`/goods/${e._id}`,e)
            this.$message({
                type:'success',
                message:'上架成功'
            })
        },
        //  权限
        flags(){
            const power = JSON.parse(window.localStorage.getItem('power'))
            this.flag = power
        },
        //  查询商品名称
        async searchName(){
            if(this.form.search == ''){
                return this.$message({
                    message:'请输入要搜索的内容',
                    type:'warning'
                })
            }
            let item = this.form.search
            const res = await this.$http.get(`goods_search/${item}`)
            this.tableData = res.data
            console.log(res)
            this.$message({
                message:`搜索到${res.data.length}条数据`,
                type:'success'
            })
        },
        //  分页
        handleSizeChange(val) {
             this.pagesize = val;
        },
        handleCurrentChange(val) {
            this.currentPage = val;
        }
    },
    
}
</script>
<style lang="less" scoped>
/deep/td{
    color: #7b7b7b;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.color-green{
    color: #4CAF50;
}
</style>